<template>
  <div>
    <stripe-checkout
        ref="checkoutRef"
        mode="payment"
        :pk="publishableKey"
        :line-items="lineItems"
        :success-url="successURL"
        :cancel-url="cancelURL"
        @loading="v => loading = v"
    />
    <button   class="btn-submit"  @click="submit">Create 100+ avatars - 40$</button>
  </div>
</template>

<script>
import { StripeCheckout } from '@vue-stripe/vue-stripe';
export default {
  components: {
    StripeCheckout,
  },
  props: ['orderType'],
  data () {
    this.publishableKey = process.env.VUE_APP_STRIPE_PUBLISHABLE_KEY;
    return {
      loading: false,
      lineItems: [
        {
          price: 'price_1M2w0fLLGjo5d30L2s1foXIG', // The id of the one-time price you created in your Stripe dashboard
          quantity: 1,
        },
      ],
      successURL: window.location.href + 'success?orderType=' + this.orderType,
      cancelURL: window.location.href + 'error',
    };
  },
  methods: {
    submit () {
      // You will be redirected to Stripe's secure checkout page
      this.$refs.checkoutRef.redirectToCheckout();
    },
  },
};
</script>
